/* Design system styles */

.design-system-card {
    border: 1px solid $grey-light;
    padding: 0;
    margin-bottom: 32px;
    box-shadow: 0 0.125rem 0.375rem 0 rgba(0, 0, 0, 0.1);
    width: auto;

    &.extra-wide {
        max-width: 1000px;
    }

    code pre {
        color: $blue-primary;
        font-size: 0.85rem;
        line-height: 26px;
        margin: 0;
    }

    &.component {
        box-shadow: none;

        .design-system-card-content {
            padding: 0;
        }

        .notes {
            border: none;
            border-bottom: 1px solid $grey-light;
            padding: 24px 32px;

            p:last-of-type {
                padding-bottom: 0;
            }
        }

        .element-design {
            border: none;
            padding: 24px 32px 24px 24px;
            background: $white;
            transition: background-color 0.2s ease;
            overflow: auto;
            position: relative;
            min-height: 96px;

            &:hover {
                cursor: pointer;
            }

            .float-button {
                position: absolute;
                top: -webkit-calc(50% - 24px);
                top: -moz-calc(50% - 24px);
                top: calc(50% - 24px);
                width: 48px;
                right: 24px;
            }

            &.closed {
                border-bottom: none;
                background: $white;
            }

            p {
                font-size: 1.2rem;
                padding: 0;
            }

            &.blue-back {
                background: $blue-primary;
            }

            .BgaIcon {
                fill: $black-light;
            }
        }

        .element-code {
            border: none;
            background: $white;
            transition: background-color 0.6s ease;
            padding-top: 0;

            &.open {
                background: $white;
            }
        }

        .example-wrapper {
            background: $grey-lightest;
            box-shadow: none;
            padding: 40px;
            display: block;
            position: relative;
            max-width: 1000px;

            @media (max-width: 650px) {
                padding: 24px;
            }

            &.white-back {
                background: $white;
                border-bottom: 1px solid $grey-light;
            }

            &.no-padding {
                padding: 0;
            }

            &.header-blue-back {
                padding: 40px 0px;
                background: $blue-primary-mid;
            }

            &.header-yellow-back {
                padding: 40px 0px;
                background: #fff4d0;
            }

            &.modal-grey {
                background-color: #adadad;
            }

            &.form-blue-back {
                background: $blue-primary;
            }

            h3 {
                padding-top: 0;
            }
        }

        .hero-pathway-example-wrapper {
            padding: 20px 15px;
        }

        .example {
            margin: 0 auto;
        }

        .content-wrapper {
            padding-right: 56px;

            p {
                text-align: left;
                margin-top: 6px;
            }
        }

        .float-button {
            width: 20%;

            .float-button-content {
                width: 48px;
                height: 48px;
                float: right;
            }

            .BgaIcon.open {
                transform: scaleY(-1);
            }
        }
    }

    &.foundation {
        box-shadow: none;

        .element-design {
            border: 1px solid $grey-light;
            padding: 24px 24px 16px 24px;
            overflow: auto;
            position: relative;
            min-height: 96px;

            @media (max-width: 600px) {
                padding: 24px 8px;
            }

            .example {
                width: -webkit-calc(100% - 48px);
                width: -moz-calc(100% - 48px);
                width: calc(100% - 48px);
                float: left;
                padding-right: 16px;
            }

            .float-button {
                width: 48px;
                padding-left: 8px;
                position: absolute;
                top: -webkit-calc(50% - 24px);
                top: -moz-calc(50% - 24px);
                top: calc(50% - 24px);
                right: 24px;

                @media (max-width: 600px) {
                    right: 8px;
                }

                .float-button-content {
                    width: 48px;
                    height: 48px;
                }

                .BgaIcon.open {
                    transform: scaleY(-1);
                }

                .BgaIcon {
                    fill: $black-light;
                }
            }

            &.blue-back {
                background-color: $blue-primary;

                &:hover {
                    background-color: $blue-primary;
                }
            }
        }
    }

    .design-system-card-header {
        background: $white;
        padding: 32px 24px 16px;
        border-top: solid 4px $blue-primary;

        h1,
        h2,
        h3,
        h4,
        h5,
        h6 {
            padding: 0;
            margin: 0;
            @include font-format($font-family, $font-semibold, normal);
        }

        @media (max-width: 450px) {
            padding: 16px;
        }
    }

    .design-system-card-content {
        padding: 24px;
        background: $white;
        border-top: none;

        .note:last-of-type {
            padding-bottom: 0;
        }

        @media (max-width: 450px) {
            padding: 16px;
        }
    }

    .design-system-card-links {
        background: $white;
        border-top: solid 1px $grey-light;
        padding: 24px;
        clear: both;

        a {
            @include font-format($font-family, $font-semibold, normal);
            color: $blue-primary;
            text-decoration: none;
            border: none;
            box-shadow: none;
            padding-left: 32px;
            background: url(../assets/Icon-arrow-right-blue.svg) no-repeat;
            background-position: left center;

            &:hover {
                box-shadow: none;
            }
        }

        &.no-height {
            height: 0;
            padding: 0;
        }

        @media (max-width: 450px) {
            padding: 16px;
        }
    }

    .element-design {
        padding: 24px;
        border: solid 1px $grey-light;
        border-bottom: none;
        background: $white;
        background-position: right 24px top 44%;
        transition: all 0.3s ease 0s;
        @include font-format($font-family, $font-semibold, normal);
        padding-left: 16px;
        display: block;

        &::before {
            content: "";
        }
    }

    .element-code {
        padding: 24px;
        border: solid 1px $grey-light;
        border-top: none;
        background: $grey-lighter;
        display: none;

        &.open {
            display: block;
        }

        a {
            color: $blue-primary;
        }
    }

    .desktop-sample {

        h1,
        h2,
        h3,
        h4,
        h5,
        p {
            margin: 0;
            padding: 0;
            color: #333333;
        }

        padding-bottom: 32px;

        h1 {
            @include font-format($font-family, $font-semibold, normal);
            font-size: 44px;
            line-height: 56px;
            padding-bottom: 16px;
        }

        h2 {
            @include font-format($font-family, $font-semibold, normal);
            font-size: 32px;
            line-height: 40px;
        }

        h3 {
            @include font-format($font-family, $font-semibold, normal);
            font-size: 21px;
            letter-spacing: 0;
            line-height: 28px;
        }

        h4 {
            @include font-format($font-family, $font-semibold, normal);
            font-size: 18px;
            letter-spacing: 0;
            line-height: 28px;
        }

        p.header-intro {
            @include font-format($font-family, $font-regular, normal);
            font-size: 18px;
            letter-spacing: 0.2px;
            line-height: 32px;
        }

        p {
            @include font-format($font-family, $font-regular, normal);
            font-size: 16px;
            letter-spacing: 0.2px;
            line-height: 28px;
        }

        p.body-mini {
            @include font-format($font-family, $font-regular, normal);
            font-size: 14px;
            letter-spacing: 0;
            line-height: 24px;
        }

        li {
            @include font-format($font-family, $font-regular, normal);
            font-size: 16px;
            letter-spacing: 0;
            line-height: 28px;
            padding-left: 1rem;

            &:last-of-type {
                padding-bottom: 16px !important;
            }
        }

        ul ul li:before {
            content: "" !important;
            height: 100%;
            margin-top: 7px;
            font-weight: 600;
            color: #666;
        }

        p.blockquote {
            @include font-format($font-family, $font-regular, normal);
            font-size: 21px;
            letter-spacing: 0.2px;
            text-align: center;
            line-height: 40px;
        }

        .pull-quote-direct svg {
            display: block;
            margin: 0 auto 24px;
        }

        .source {
            @include font-format($font-family, $font-regular, italic);
            color: $grey-darker;
            padding-top: 8px;
            font-size: 16px;
            line-height: 24px;

            hr {
                border-color: $grey-darker;
                border-top-width: 0.125rem;
                display: inline-block;
                height: 0.125rem;
                line-height: 24px;
                margin: 0 0.125rem 0 0;
                opacity: 0.6;
                vertical-align: middle;
                width: 1.5625rem;
                padding: 0;
            }
        }

        .hr-bottom {
            background-color: $yellow-primary-mid;
            border-top: 0;
            height: 0.125rem;
            margin: 0 auto;
            width: 2.625rem;
            margin-top: 24px;
            padding: 0;
        }

        p.header-intro {
            @include font-format($font-family, $font-regular, normal);
            font-size: 18px;
            letter-spacing: 0.2px;
            line-height: 30px;
        }

        p.header-update {
            @include font-format($font-family, $font-regular, italic);
            font-size: 12px;
            letter-spacing: 0.2px;
            line-height: 18px;
        }

        p.header-label {
            @include font-format($font-family, $font-regular, normal);
            font-size: 12px;
            letter-spacing: 0.2px;
            line-height: 20px;
            text-transform: uppercase;
        }
    }

    .mobile-sample {

        h1,
        h2,
        h3,
        h4,
        h5,
        p {
            margin: 0;
            padding: 0;
            color: $black-light;
        }

        padding-bottom: 32px;

        h1 {
            @include font-format($font-family, $font-semibold, normal);
            font-size: 32px;
            letter-spacing: 0;
            line-height: 36px;
        }

        h2 {
            @include font-format($font-family, $font-semibold, normal);
            font-size: 24px;
            line-height: 30px;
        }

        h3 {
            @include font-format($font-family, $font-semibold, normal);
            font-size: 21px;
            letter-spacing: 0;
            line-height: 32px;
        }

        h4 {
            @include font-format($font-family, $font-semibold, normal);
            font-size: 18px;
            letter-spacing: 0;
            line-height: 28px;
        }

        h5 {
            line-height: 24px;
        }

        p {
            @include font-format($font-family, $font-regular, normal);
            font-size: 16px;
            letter-spacing: 0.2px;
            line-height: 28px;
        }

        p.body-mini {
            @include font-format($font-family, $font-regular, normal);
            font-size: 14px;
            letter-spacing: 0;
            line-height: 24px;
        }

        li {
            @include font-format($font-family, $font-regular, normal);
            font-size: 16px;
            letter-spacing: 0.2px;
            line-height: 24px;
            padding-left: 1rem;

            &:last-of-type {
                padding-bottom: 16px !important;
            }
        }

        ul ul li:before {
            content: "" !important;
            height: 100%;
            margin-top: 7px;
            font-weight: 600;
            color: #666;
        }

        p.blockquote {
            @include font-format($font-family, $font-regular, normal);
            font-size: 18px;
            letter-spacing: 0.2px;
            text-align: center;
            line-height: 24px;
        }

        .pull-quote-direct svg {
            display: block;
            margin: 0 auto 24px;
        }

        .source {
            @include font-format($font-family, $font-regular, italic);
            color: $grey-darker;
            padding-top: 8px;
            font-size: 16px;
            line-height: 24px;

            hr {
                border-color: $grey-darker;
                border-top-width: 0.125rem;
                display: inline-block;
                height: 0.125rem;
                margin: 0 0.125rem 0 0;
                opacity: 0.6;
                vertical-align: middle;
                width: 1.5625rem;
                padding: 0;
            }
        }

        .hr-bottom {
            background-color: $yellow-primary-mid;
            border-top: 0;
            height: 0.125rem;
            margin: 0 auto;
            width: 2.625rem;
            margin-top: 24px;
            padding: 0;
        }

        p.header-intro {
            @include font-format($font-family, $font-regular, normal);
            font-size: 16px;
            letter-spacing: 0.2px;
            line-height: 28px;
        }

        p.header-update {
            @include font-format($font-family, $font-regular, italic);
            font-size: 12px;
            letter-spacing: 0.2px;
            line-height: 16px;
        }

        p.header-label {
            @include font-format($font-family, $font-regular, normal);
            font-size: 12px;
            letter-spacing: 0.2px;
            line-height: 20px;
            text-transform: uppercase;
        }
    }

    .form-elements {
        .form-example-container {
            label {
                width: 100%;
                display: block;
                font-size: 16px;
                padding: 0 0 16px 0;
                margin: 0;
            }

            .error-message {
                display: none;

                &.show {
                    display: block;
                    color: $red-negative-dark;
                    margin: 0;
                    padding: 16px 0 0 0;
                }
            }

            .help-wrapper {

                p,
                li {
                    font-size: 14px;
                    line-height: 24px;
                    padding-bottom: 16px;
                    color: $grey-darker;
                }

                &.complex {
                    .contextual-help {
                        cursor: pointer;
                        color: $blue-primary-mid;
                        text-decoration: underline;
                        padding: 0 0 0 0;
                        margin-bottom: 16px;

                        &:before {
                            content: url(../assets/BGAIcon-chevron-right-sml.svg);
                            margin: 0 8px 0 4px;
                            display: inline-block;
                        }

                        &.open {
                            &:before {
                                transform: rotate(90deg);
                                margin: 0 10px 0 4px;
                            }
                        }
                    }

                    .complex-answer {
                        display: none;
                        border-left: solid 2px $blue-primary;
                        margin: 0 0 16px 8px;
                        padding-left: 8px;

                        p:last-of-type {
                            padding-bottom: 0;
                        }
                    }
                }
            }

            input {
                height: 52px;
                width: 100%;
                padding: 10px;

                &:focus {
                    outline: none;
                    border: solid 3px $blue-primary !important;
                    background: none !important;
                }

                &[type="text"] {
                    @include font-format($font-family, $font-regular, normal);
                    font-size: 16px;
                }
            }

            #form-text-area {
                width: 100% !important;
                padding: 10px;
                min-height: 250px;
                display: block;

                &:focus {
                    outline: none;
                    border: solid 3px $blue-primary !important;
                    background: none !important;
                }

                &[type="text"] {
                    @include font-format($font-family, $font-regular, normal);
                    font-size: 16px;
                    letter-spacing: 0;
                }
            }

            .textarea-char-count {
                color: $grey-darker;
                padding: 8px 0 0 0;
                margin: 0;

                &.excess-count {
                    display: block;
                    color: $red-negative-dark;
                    margin: 0;
                    padding: 8px 0 0 0;
                    font-size: 16px;
                    text-align: left;
                }
            }

            mark {
                background: $red-negative-light;
            }

            // CSS Styles from https://github.com/lonekorean/highlight-within-textarea
            //Thank you lone Korean!!!
            .hwt-container {
                display: inline-block;
                position: relative;
                overflow: hidden !important;
                -webkit-text-size-adjust: none !important;
                width: 100%;
            }

            .hwt-backdrop {
                position: absolute !important;
                top: 0px !important;
                right: 0px !important;
                bottom: 0 !important;
                left: 0 !important;
                width: 100%;
                padding: 10px !important;
                overflow-x: hidden !important;
                overflow-y: auto !important;
            }

            .hwt-highlights {
                width: auto !important;
                height: auto !important;
                border-color: transparent !important;
                white-space: pre-wrap !important;
                word-wrap: break-word !important;
                color: transparent !important;
                overflow: hidden !important;
            }

            .hwt-input {
                display: block !important;
                position: relative !important;
                margin: 0;
                padding: 0;
                border-radius: 0;
                font: inherit;
                overflow-x: hidden !important;
                overflow-y: auto !important;
            }

            .hwt-content {
                border: 1px solid;
                background: none transparent !important;
                padding: 2px 0 0 1px;
                @include font-format($font-family, $font-regular, normal);
                font-size: 16px;
                letter-spacing: 0;
            }

            .hwt-content mark {
                padding: 0px !important;
                color: $red-negative-light;

                &.hide-mark {
                    display: none !important;
                }
            }

            .text-box-with-units {
                span {
                    height: 52px;
                    width: auto;
                    padding: 10px 15px;
                    color: $grey-darker;
                    @include font-format($font-family, $font-regular, normal);
                    border: solid 1px $darkblue-mid;
                    display: inline-block;
                    background: $grey-lightest;
                    vertical-align: middle;
                }

                input {
                    width: 80%;
                    max-width: 150px;
                    display: inline-block;
                    text-align: right;
                    border-left: none !important;
                    border-right: none !important;
                    vertical-align: middle;

                    @media (max-width: 650px) {
                        width: 40%;
                    }
                }
            }

            select {
                height: 52px;
                width: 100%;
                max-width: 400px;
                padding: 10px 15px;
                color: $black-light;
                background: $white url(../assets/BGAIcon-chevron-down.svg) no-repeat;
                background-size: 16px 10px;
                background-position: right 20px center;
                border-radius: 0px;
                -webkit-appearance: none !important;
                -webkit-border-radius: 0px !important;

                &:focus {
                    outline: none;
                    border: solid 3px $blue-primary !important;
                }

                &[type="text"] {
                    @include font-format($font-family, $font-regular, normal);
                    font-size: 16px;
                }
            }

            select::-ms-expand {
                display: none !important;
            }

            .radio-item {
                border: solid 1px $darkblue-mid;
                min-height: 52px;
                width: 100%;
                max-width: 350px;
                padding: 10px 15px;
                line-height: 0;
                display: block;
                margin-bottom: 16px;

                &:last-of-type {
                    margin-bottom: 0;
                }

                input[type="radio"] {
                    opacity: 0;
                    width: 0;
                    height: 22px;
                }

                label {
                    position: relative;
                    padding: 0 0 0 32px;
                    cursor: pointer;
                    display: inline-block;
                    line-height: 20px;

                    &:before {
                        height: 24px;
                        width: 24px;
                        content: " ";
                        background: url(../assets/radio-not-selected.svg) no-repeat;
                        background-size: 24px 24px;
                        position: absolute;
                        top: calc(50% - 12px);
                        left: 0;
                    }
                }

                input[checked="checked"] + label:before {
                    background: url(../assets/radio-selected.svg) no-repeat;
                    background-size: 24px 24px;
                }
            }

            .checkbox-item {
                line-height: 28px;
                display: block;
                margin-bottom: 16px;

                &:last-of-type {
                    margin-bottom: 0;
                }

                input[type="checkbox"] {
                    opacity: 0;
                    width: 0;
                    height: 28px;
                }

                label {
                    position: relative;
                    padding-left: 42px;
                    cursor: pointer;
                    display: inline-block;
                    padding-bottom: 0;

                    &:before {
                        height: 28px;
                        width: 28px;
                        content: " ";
                        border: solid 1px $darkblue-mid;
                        position: absolute;
                        top: 0;
                        left: 0;
                    }
                }
            }

            .button-wrapper {
                width: auto;
                display: inline-block;
                transition-property: transform, -webkit-transform;
                transition-duration: 0.4s, 0.4s;
                transition-timing-function: ease, ease;
                transition-delay: 0s, 0s;
                margin-top: 8px;

                &:hover {
                    transform: translateY(-0.1875rem);
                }

                .cta-button {
                    box-shadow: rgba(0, 0, 0, 0.1) 0px 2px 4px 0px;
                    border-radius: 6px;
                    opacity: 1;
                    padding: 8px 32px;
                    text-align: center;
                    letter-spacing: 0;
                    background: $blue-primary-mid 0% 0% no-repeat;
                    border: 2px solid $blue-primary;
                    color: $white;
                    @include font-format($font-family, $font-semibold, normal);

                    &:hover,
                    &:active {
                        box-shadow: rgba(0, 0, 0, 0.3) 0px 4px 8px 0px;
                        border-radius: 6px;
                        opacity: 1;
                    }

                    &:hover {
                        background: $blue-primary 0% 0% no-repeat;
                        border: 2px solid $blue-active;
                    }

                    &:active {
                        background: $blue-active 0% 0% no-repeat;
                        border: 2px solid $blue-active;
                    }
                }

                .secondary-button {
                    @include font-format($font-family, $font-semibold, normal);
                    background: $white 0% 0% no-repeat padding-box;
                    border: 2px solid $blue-primary-mid;
                    color: $blue-primary-mid;

                    &:hover {
                        background: $blue-primary 0% 0% no-repeat padding-box;
                        border: 2px solid $blue-active;
                        color: $white;
                    }

                    &:active {
                        background: $blue-active 0% 0% no-repeat padding-box;
                        color: $white;
                    }
                }

                .tertiary-link {
                    @include font-format($font-family, $font-semibold, normal);
                    color: $blue-primary;
                    text-decoration: underline;
                    box-shadow: none;
                    margin-right: 40px;
                }

                .add-field {
                    @include font-format($font-family, $font-regular, normal);
                    color: $black-light;
                    text-decoration: none;
                    box-shadow: none;
                    margin-right: 40px;
                    padding: 4px 0 4px 40px;
                    position: relative;
                    display: inline-block;

                    &:hover,
                    &:active {
                        @include font-format($font-family, $font-semibold, normal);
                        color: $blue-primary;

                        &:before {
                            background: url(../assets/Icon-add-circle-blue.svg) no-repeat;
                        }
                    }

                    &:before {
                        background: url(../assets/Icon-add-circle-mid-blue.svg) no-repeat;
                        content: " ";
                        height: 32px;
                        width: 32px;
                        display: block;
                        position: absolute;
                        top: calc(50% - 16px);
                        left: 0;
                    }
                }

                &[data-state="hover-state"] {
                    transform: translateY(-0.1875rem);

                    .cta-button {
                        box-shadow: rgba(0, 0, 0, 0.3) 0px 4px 8px 0px;
                        border-radius: 6px;
                        opacity: 1;
                        color: $white;
                        background: $blue-primary 0% 0% no-repeat;
                        border: 2px solid $blue-active;
                    }

                    &.tertiary-button-wrapper {
                        transform: translateY(0px);
                    }

                    &.add-field-wrapper {
                        transform: translateY(0px);

                        .add-field {
                            @include font-format($font-family, $font-semibold, normal);
                            color: $blue-primary;

                            &:before {
                                background: url(../assets/Icon-add-circle-blue.svg) no-repeat;
                            }
                        }
                    }
                }

                &[data-state="active-state"] {
                    transform: translateY(-0.1875rem);

                    .cta-button {
                        box-shadow: rgba(0, 0, 0, 0.3) 0px 4px 8px 0px;
                        color: $white;
                        border-radius: 6px;
                        opacity: 1;
                        background: $blue-active 0% 0% no-repeat;
                        border: 2px solid $blue-active;
                    }

                    &.tertiary-button-wrapper {
                        transform: translateY(0px);

                        .tertiary-link {
                            color: $blue-active;
                        }
                    }

                    &.add-field-wrapper {
                        transform: translateY(0px);

                        .add-field {
                            @include font-format($font-family, $font-semibold, normal);
                            color: $blue-active;

                            &:before {
                                background: url(../assets/Icon-add-circle-blue-active.svg) no-repeat;
                            }
                        }
                    }
                }

                &[data-state="disabled-state"] {
                    transform: translateY(0rem);

                    .cta-button {
                        box-shadow: none;
                        border-radius: 6px;
                        border: 2px solid $grey;
                        opacity: 1;
                        background: $white;
                        color: $grey-dark;
                    }

                    &.tertiary-button-wrapper {
                        .tertiary-link {
                            color: $grey-dark;
                        }
                    }

                    &.add-field-wrapper {
                        .add-field {
                            color: $grey-dark;

                            &:before {
                                background: url(../assets/Icon-add-circle-grey.svg) no-repeat;
                            }
                        }
                    }
                }
            }

            #datepicker {
                .input-group {
                    position: relative;
                    display: block;
                    width: 340px;
                    height: 52px;
                    z-index: 2000;

                    input {
                        border-radius: 0;
                        width: 288px;
                        display: inline-block;
                        position: absolute;
                        top: 0;
                        left: 0;
                        box-shadow: none;
                        background: $white;
                    }

                    .input-group-addon {
                        height: 52px;
                        width: 52px;
                        border: solid 1px $darkblue-mid;
                        border-left: none;
                        display: inline-block;
                        position: absolute;
                        top: 0;
                        right: 0;
                        background: url(../assets/BGAIcon-calendar-blue-mid.svg) no-repeat center center;

                        &:hover {
                            background: url(../assets/BGAIcon-calendar-blue.svg) no-repeat center center;
                            border: solid 3px $blue-primary;
                        }
                    }
                }

                @media (max-width: 650px) {
                    .input-group {
                        width: 250px;

                        input {
                            width: 200px;
                        }
                    }
                }
            }

            .datepicker {
                width: 340px !important;
                padding: 16px;
                border: 3px solid $blue-primary;
                border-top: none;
                border-radius: 0;
                box-shadow: none;
                width: auto;
                display: block;
                left: 40px !important;
                top: 134px !important;
                z-index: 1999;

                &:before {
                    display: none;
                }

                .table-condensed {
                    font-size: 15px;
                    padding: 16px;
                    width: 100%;

                    td,
                    th {
                        border-radius: 0;
                    }

                    .datepicker-switch {
                        padding: 8px;
                        height: 44px;
                        font-size: 16px;
                        text-align: center;
                        @include font-format($font-family, $font-semibold, normal);
                        color: $blue-primary;
                        cursor: pointer;

                        &:hover {
                            background: $grey-lighter;
                        }
                    }

                    .prev {
                        text-indent: -9999px;
                        background: url(../assets/BGAIcon-chevron-left-blue.svg) no-repeat;
                        background-position: center center;
                        background-size: 10px 16px;
                        cursor: pointer;

                        &:hover {
                            background-color: $grey-lighter;
                        }
                    }

                    .next {
                        text-indent: -9999px;
                        background: url(../assets/BGAIcon-chevron-right-blue.svg) no-repeat;
                        background-position: center center;
                        background-size: 10px 16px;

                        &:hover {
                            background-color: $grey-lighter;
                        }
                    }

                    .dow {
                        color: $blue-primary;
                        @include font-format($font-family, $font-semibold, normal);
                    }

                    .day {
                        padding: 8px;
                        height: 44px;
                        width: 44px;
                        text-align: center;

                        &.active {
                            background: $blue-primary-mid;
                            color: $white;
                            @include font-format($font-family, $font-semibold, normal);
                        }

                        &.today {
                            background: $grey-lighter;
                            opacity: 1;
                        }

                        &.old,
                        &.new {
                            color: $grey;
                        }

                        &:hover {
                            background: $blue-primary;
                            color: $white;
                            @include font-format($font-family, $font-semibold, normal);
                        }
                    }

                    span {
                        border-radius: 0;
                        margin: 0;
                        padding: 8px 0;
                        line-height: 34px;
                        width: 25%;

                        &.focused,
                        &.active {
                            background: $blue-primary-mid;
                            color: $white;
                            @include font-format($font-family, $font-semibold, normal);
                        }

                        &:hover {
                            background: $blue-primary;
                            color: $white;
                            @include font-format($font-family, $font-semibold, normal);
                        }
                    }
                }

                @media (max-width: 650px) {
                    width: 250px !important;
                    top: 116px !important;
                    left: 24px !important;
                    padding: 8px;

                    .table-condensed {
                        .day {
                            padding: 6px;
                            height: 36px;
                            width: 36px;
                        }
                    }
                }
            }

            &.text-box-button-combo-example {
                position: relative;
                -webkit-transform: none;
                transform: none;

                input {
                    max-width: 350px;
                    display: inline-block;
                    vertical-align: middle;

                    @media (max-width: 850px) {
                        max-width: 250px;
                    }

                    @media (max-width: 700px) {
                        max-width: 150px;
                    }

                    @media (max-width: 400px) {
                        max-width: 150px;
                    }
                }

                .combo-button-wrapper {
                    display: inline-block;
                    vertical-align: middle;

                    .combo-button {
                        border-radius: 6px;
                        border-top-left-radius: 0px;
                        border-bottom-left-radius: 0px;
                        box-shadow: none;
                        height: 52px;
                        display: block;
                        border: solid 1px $darkblue-mid;
                        margin: 0 0 0 -6px;
                        padding: 10px 24px 10px 48px;
                        text-align: center;
                        color: $white;
                        @include font-format($font-family, $font-semibold, normal);

                        &:hover {
                            background: $blue-primary no-repeat;
                        }

                        &:active {
                            background: $blue-active no-repeat;
                        }

                        @media (max-width: 400px) {
                            padding: 10px 12px 10px 32px;
                        }
                    }

                    .primary-combo {
                        background: $blue-primary-mid url(../assets/Icon-plus-white.svg) no-repeat;
                        background-position: left 24px center;

                        &:hover {
                            background: $blue-primary url(../assets/Icon-plus-white.svg) no-repeat;
                            background-position: left 24px center;
                        }

                        &:active {
                            background: $blue-active url(../assets/Icon-plus-white.svg) no-repeat;
                            background-position: left 24px center;
                        }

                        @media (max-width: 400px) {
                            background-position: left 12px center;

                            &:hover,
                            &:active {
                                background-position: left 12px center;
                            }
                        }
                    }

                    .secondary-combo {
                        background: $white url(../assets/Icon-search-blue.svg) no-repeat;
                        background-position: left 24px center;
                        color: $blue-primary-mid;

                        &:hover {
                            background: $blue-primary url(../assets/Icon-search-white.svg) no-repeat;
                            background-position: left 24px center;
                            color: $white;
                        }

                        &:active {
                            background: $blue-active url(../assets/Icon-search-white.svg) no-repeat;
                            background-position: left 24px center;
                            color: $white;
                        }

                        @media (max-width: 400px) {
                            background-position: left 12px center;

                            &:hover,
                            &:active {
                                background-position: left 12px center;
                            }
                        }
                    }
                }
            }

            &[data-state="default-state"] {

                input,
                #form-text-area,
                .text-box-with-units span,
                select,
                .checkbox-item input + label:before,
                .combo-button {
                    border: solid 1px $darkblue-mid;
                }

                .error-message {
                    display: none;
                }

                #datepicker {
                    .datepicker {
                        display: none !important;
                    }
                }
            }

            &[data-state="focus-state"] {

                input,
                #form-text-area,
                .text-box-with-units span,
                select,
                .checkbox-item input[data-state="focus-state"] + label:before,
                .combo-button {
                    outline: none;
                    border: solid 3px $blue-primary;
                }

                .text-box-with-units input {
                    border-right: 0 !important;
                    border-left: 0 !important;
                }

                &.text-box-button-combo-example .combo-button-wrapper #primary-combo {
                    background: $blue-primary url(../assets/Icon-plus-white.svg) no-repeat;
                    background-position: left 24px center;
                    color: $white;
                    border: solid 3px $blue-primary;
                    border-left: 0px;
                    margin-left: -5px;
                }

                &.text-box-button-combo-example .combo-button-wrapper #secondary-combo {
                    background: $blue-primary url(../assets/Icon-search-white.svg) no-repeat;
                    background-position: left 24px center;
                    color: $white;
                    border: solid 3px $blue-primary;
                    border-left: 0px;
                    margin-left: -5px;
                }

                .checkbox-item[data-state="focus-state"] input + label {
                    color: $blue-primary;
                    @include font-format($font-family, $font-semibold, normal);

                    &:before {
                        background: url(../assets/Icon-tick-primary-blue.svg) no-repeat;
                        background-size: 16px 12px;
                        background-position: center center;
                    }
                }

                .error-message {
                    display: none;
                }

                #datepicker {
                    .input-group {
                        box-shadow: none !important;

                        .input-group-addon {
                            border: solid 3px $blue-primary;
                            border-left: none;
                        }
                    }
                }
            }

            &[data-state="active-state"] {

                input,
                .combo-button {
                    outline: none;
                    border: solid 3px $blue-primary;
                }

                &.text-box-button-combo-example .combo-button-wrapper #primary-combo {
                    background: $blue-active url(../assets/Icon-plus-white.svg) no-repeat;
                    background-position: left 24px center;
                    color: $white;
                    border: solid 3px $blue-primary;
                    border-left: 0px;
                    margin-left: -5px;
                }

                &.text-box-button-combo-example .combo-button-wrapper #secondary-combo {
                    background: $blue-active url(../assets/Icon-search-white.svg) no-repeat;
                    background-position: left 24px center;
                    color: $white;
                    border: solid 3px $blue-primary;
                    border-left: 0px;
                    margin-left: -5px;
                }
            }

            &[data-state="error-state"] {

                input,
                #form-text-area,
                .text-box-with-units span,
                select,
                .checkbox-item input + label:before,
                .combo-button {
                    outline: none;
                    border: solid 3px $red-negative-dark !important;
                }

                .text-box-with-units input {
                    border-left: 0px !important;
                    border-right: 0px !important;
                }

                &.text-box-button-combo-example .combo-button-wrapper #primary-combo,
                &.text-box-button-combo-example .combo-button-wrapper #secondary-combo {
                    border: solid 3px $red-negative-dark;
                    background-color: $blue-primary-mid;
                    color: $white;
                    border-left: 0px;
                    margin-left: -5px;
                }

                &.text-box-button-combo-example .combo-button-wrapper #secondary-combo {
                    background-image: url(../assets/Icon-search-white.svg);
                }

                .error-message {
                    display: block;
                    color: $red-negative-dark;
                    margin: 0;
                    padding: 16px 0 0 0;

                    &.error-hidden {
                        display: none;
                    }

                    &.textarea-error-message {
                        padding-top: 8px;
                    }
                }

                .textarea-char-count.under {
                    display: none;
                }

                #datepicker {
                    .input-group {
                        .input-group-addon {
                            border: solid 3px $red-negative-dark;
                            border-left: none;
                        }
                    }

                    .datepicker {
                        border: solid 3px $red-negative-dark;
                        border-top: none;
                    }
                }
            }

            &[data-state="disabled-state"] {

                input,
                #form-text-area,
                .text-box-with-units span,
                select,
                .checkbox-item input + label:before,
                .combo-button {
                    outline: none;
                    border: solid 1px $grey-headers !important;
                    background-color: $grey-lightest !important;
                    color: $grey-dark !important;
                }

                .checkbox-item label {
                    color: #8e8e8e;
                }

                &.text-box-button-combo-example .combo-button-wrapper #primary-combo {
                    background: $grey-lightest url(../assets/Icon-plus-grey.svg) no-repeat;
                    background-position: left 24px center;
                }

                &.text-box-button-combo-example .combo-button-wrapper #secondary-combo {
                    background: $grey-lightest url(../assets/Icon-search-grey.svg) no-repeat;
                    background-position: left 24px center;
                }

                select {
                    background: $grey-lightest url(../assets/Icon-chevron-down-grey.svg) no-repeat;
                    background-size: 16px 10px;
                    background-position: right 20px center;
                }

                .error-message {
                    display: none;
                }

                .excess-count {
                    display: none;
                }

                #datepicker {
                    .input-group {
                        .input-group-addon {
                            border: solid 1px $grey-headers !important;
                            border-left: none;
                            background: $grey-lightest url(../assets/BGAIcon-calendar-grey.svg) no-repeat center center;
                        }
                    }

                    .datepicker {
                        display: none !important;
                    }
                }
            }

            .radio-item {
                &[data-state="default-state"] {
                    border: solid 1px $darkblue-mid;
                }

                &[data-state="focus-state"] {
                    outline: none;
                    border: solid 3px $blue-primary;

                    input + label {
                        color: $blue-primary;
                        @include font-format($font-family, $font-semibold, normal);

                        &:before {
                            background: url(../assets/radio-selected.svg) no-repeat;
                            background-size: 24px 24px;
                        }
                    }
                }

                &[data-state="error-state"] {
                    outline: none;
                    border: solid 3px $red-negative-dark;
                }

                &[data-state="disabled-state"] {
                    outline: none;
                    border: solid 1px $grey-headers;
                    background: $grey-lightest;
                    color: $grey-dark;
                }
            }

            .checkbox-item {
                &[data-state="default-state"] {
                    input + label:before {
                        border: solid 1px $darkblue-mid;
                    }
                }

                &[data-state="focus-state"] {
                    input + label:before {
                        border: solid 3px $blue-primary;
                        background: url(../assets/Icon-tick-primary-blue.svg) no-repeat;
                        background-size: 16px 12px;
                        background-position: center center;
                    }
                }

                &[data-state="error-state"] {
                    input + label:before {
                        border: solid 3px $red-negative-dark;
                        background: $white;
                    }
                }

                &[data-state="disabled-state"] {
                    border: solid 1px $grey-headers !important;
                    background-color: $grey-lightest !important;
                    color: $grey-dark !important;
                }
            }
        }
    }

    .dynamic-form-elements {

        h1,
        h2,
        h3,
        h4,
        p,
        label,
        a {
            color: $white;
        }

        .dynamic-hide {
            display: none;
        }

        p {
            font-size: 18px;

            &.contextual-help {
                font-size: 12px;
                padding: 0;
                line-height: 1rem;
            }
        }

        label {
            font-size: 32px;
            @include font-format($font-family, $font-semibold, normal);
            margin: 0 16px 0 0;
        }

        input {
            margin: 0 0 16px 0;
            color: $white;

            &:focus {
                border: solid 2px $white;

                &::placeholder {
                    color: transparent !important;
                }
            }
        }

        select {
            margin: 0;
            @include font-format($font-family, $font-semibold, normal);
            font-size: 20px;
            color: $white;
            background-position: center right 8px;
            background-size: 16px 16px;
        }

