        .multi-select-group {
            margin-top: 16px;

            button {
                padding: 6px 16px 6px 40px;

                &.not-selected {
                    background: $blue-primary-mid url(../assets/Icon-DF-plus-lightblue.svg) no-repeat;
                    background-position: center left 16px;

                    &:hover {
                        background: $blue-primary url(../assets/Icon-DF-plus-white.svg) no-repeat;
                        background-position: center left 16px;
                    }

                    &:active {
                        background: $blue-active url(../assets/Icon-DF-plus-white.svg) no-repeat;
                        background-position: center left 16px;
                    }
                }

                &.selected {
                    background: $white url(../assets/Icon-DF-tick-blue.svg) no-repeat;
