                    p.contextual-help {
                        color: $blue-primary;
                    }

                    input {
                        color: $blue-primary;
                        border-bottom: solid $red-negative-dark 2px;

                        &::placeholder {
                            color: #7983aa;
                        }

                        &:focus {
                            border: solid $red-negative-dark 2px;
                        }
                    }

                    select {
                        color: $blue-primary;
                        border-bottom: solid $red-negative-dark 2px;
                        background: url(../assets/Icon-chevron-down-blue.png) no-repeat center right 8px;
                        background-size: 16px 10px;

                        &:focus {
                            border: solid $red-negative-dark 2px;
                        }
                    }

                    .validation-message {
                        display: block;
                        color: $red-negative-dark;
                        margin-top: 8px;
                        padding: 0;
                        font-size: 16px;
                        @include font-format($font-family, $font-regular, normal);
