                button#supportPathwaySearchButton {
                    border: solid 0.125rem $white !important;
                    background-color: $blue-primary;
                    border: 0.125rem solid #18335c !important;
                    box-shadow: 0 0.25rem 0.5rem 0 rgba(0, 0, 0, 0.3) !important;
                    transform: translateY(-0.25rem);
                    color: $white;
                    border: solid 0.125rem $white !important;
                }
            }

            &[data-state="active-state"] {
                .multi-select-group {
                    button {
                        background: $blue-active url(../assets/Icon-DF-plus-white.svg) no-repeat;
                        background-position: center left 16px;
                        box-shadow: 0 0.1875rem 0.375rem 0 rgba(0, 0, 0, 0.2);
                        transform: translateY(-0.125rem);
                        border-color: $white;
                    }
                }

                button#supportPathwaySearchButton {
                    background-color: $blue-active !important;
                    color: $white;
                    border-color: $white;
                    transform: translateY(-0.125rem);
                    box-shadow: 0 0.1875rem 0.375rem 0 rgba(0, 0, 0, 0.2);
                }
            }

            &[data-state="selected-state"] {
                .multi-select-group {
                    button {
                        background: $white url(../assets/Icon-DF-tick-blue.svg) no-repeat;
                        background-position: center left 16px;
                        color: $black;
                        border-color: $white;
                    }
                }
            }
        }
    }

