    .notification {
        &.notification-information {
            .notification-wrapper {
                background: $blue-lightest;
                border-top: 4px solid #b3c1d7;
            }
        }

        &.notification-confirmation {
            .notification-wrapper {
                background: $green-positive-lightest;
                border-top: 4px solid $green-positive-light;
            }
        }

        &.notification-warning {
            .notification-wrapper {
                background: $red-negative-lightest;
                border-top: 4px solid $red-negative-light;
            }
        }

        .notification-wrapper {
            position: relative;
            display: flex;
            padding: 24px;

