        }

        .notification-icon {
            padding-right: 16px;
            height: 56px;
            width: 56px;
            min-width: 56px;
        }

        .notification-content {
            padding: 8px 0 0 16px;

            p {
                padding-bottom: 16px;

                &:last-of-type {
                    padding-bottom: 0;
                }
            }

            &.with-close {
                padding-right: 16px;
            }
        }

        .notification-close {
            margin-top: 0px;
            background: url(../assets/BGAIcon-close.svg) no-repeat;
            background-size: 16px 16px;
            height: 16px;
            min-width: 16px;
            width: 16px;
            position: absolute;
            top: 16px;
            right: 16px;
