    /* Mini-list */
    .mini-list {
        width: 100%;
        height: auto;

        .mini-list-item {
            margin-bottom: 4px;

            &.active {
                .title-container {
                    background: #d5e2f2;
                    outline: none;
                }

                .content-container {
                    display: block;
                }

                .arrow-svg {
                    transform: scaleY(-1);
                }
            }

            .title-container {
                display: flex;
                justify-content: flex-start;
                align-items: center;
                background: #e5edf7;
                padding: 16px 24px 16px 16px;
                cursor: pointer;

                .title {
                    margin: 0 16px;
                    padding: 0;
                    font-size: 18px;
                    line-height: 24px;
                    font-family: "Open Sans", sans-serif;
                    font-weight: 600;
                }

                &:hover,
                &:focus {
                    background: #d5e2f2;
                    outline: none;

                    .title {
                        text-decoration: underline;
                    }
                }
            }

            .svg-container {
                display: flex;
                align-items: center;
                justify-content: center;

                &.tick-svg {
                    width: 32px;
                    height: 32px;
                    align-self: flex-start;
                }

                &.arrow-svg {
                    margin-left: auto;
                    width: 20px;
                    height: 13px;
                    transition: transform 0.3s;
                }
            }

            .content-container {
                background: #d5e2f2;
                padding: 0 24px 24px 64px;
                letter-spacing: 0.2px;
                font-size: 16px;
                line-height: 28px;
                font-family: "Open Sans", sans-serif;
                display: none;

                .content {
                    animation: fadeInTop 0.6s forwards;

                    > *:last-child li:last-of-type, > *:last-child {
                        padding-bottom: 0px;
                    }
                }

                p {
                    margin: 0;
                    padding: 0 0 32px 0;
                }

                a {
                    color: #254f90;
                    box-shadow: none;
                    border-bottom: 2px solid #748fb8;
                    text-decoration: none;
                    transition: box-shadow 250ms;
                    font-weight: 600;
                    display: inline;

                    &:hover,
                    &:focus {
                        box-shadow: #fff 0px -20px 0px 0px inset;
                        text-decoration: none;
                        cursor: pointer;
                        outline: none;
                    }
                }

                ul {
                    padding: 0 0 0 32px;
                    margin: 0;
                    line-height: 1.78;
                    list-style: none;

                    li {
                        padding-bottom: 16px;
                        margin: 0;
                        line-height: 32px;
                        position: relative;

                        p {
                            padding: 0px;
                            display: inline;
                        }

                        svg {
                            position: absolute;
                            left: 0px;
                            top: 10px;
                        }
                    }
                }
            }
        }
    }

    .mobile-example {
        .mini-list {
            .mini-list-item {
                .title-container {
                    padding: 16px 8px 16px 8px;

                    .title {
                        margin: 0 8px;
                        font-size: 16px;
                    }

                    .tick-svg {
                        width: 24px;
                        height: 24px;
                    }
                }

                .content-container {
                    padding: 0 24px 16px 40px;
                    font-size: 16px;
                }
            }
        }
    }

    @media (max-width: 640px) {
        .mini-list {
            .mini-list-item {
                .title-container {
                    padding: 16px 8px 16px 8px;

                    .title {
                        margin: 0 8px;
                        font-size: 16px;
                    }

                    .tick-svg {
                        width: 24px;
                        height: 24px;
                    }
                }

                .content-container {
                    padding: 0 24px 16px 40px;
                    font-size: 16px;
                }
            }
        }
    }

