        color: unset;
        box-shadow: none;
        background-color: none;
        border: none;
    }

    .table {
        .table-responsive {
            overflow-y: hidden;

            th {
                @include font-format($font-family, $font-semibold, normal);
            }

            margin-bottom: 32px;
        }

        @media (max-width: 1000px) {
            .generic-table {

                table,
                thead,
                tbody,
                th,
                td,
                tr {
                    display: block;
                }

                /* Hide table headers (but not display: none;, for accessibility) */
                thead tr {
                    position: absolute;
                    top: -9999px;
                    left: -9999px;
                }

                td {
                    display: none;
                    position: relative;
                    padding-left: 30%;
                    white-space: normal;
                    text-align: left;
                    border-top: 0px;

                    &::before {
                        position: absolute;
                        top: 16px;
                        left: 16px;
                        width: 25%;
                        text-align: left;
                        content: attr(data-title);
                        display: block;
                    }
                }

                tr {
                    display: table;
                    width: 100%;
                    border-bottom: solid 1px $grey-light;

                    td {
                        background: $grey-lighter;

                        &:first-of-type {
                            background: $white url(../assets/Icon-chevron-down-333333.svg) no-repeat;
                            background-position: right 16px center;
                            background-size: 16px 10px;
                            font-weight: 600;
                            padding-left: 16px;
                            padding-right: 50px;
                            display: block;

                            &::before {
                                content: "";
                            }
                        }
                    }
                }

                .showGroup {
                    display: block;
                    transition: all 0.2s;
                }

                .groupParent td {
                    &:first-of-type {
                        background: $grey-lighter url(../assets/Icon-chevron-up-333333.svg) no-repeat;
                        background-position: right 16px center;
                        background-size: 16px 10px;
                    }
                }
            }
        }

        @media (max-width: 800px) {
            .generic-table {
                td {
                    padding-left: 45%;
                    font-size: 14px;
                    line-height: 1.4rem;

                    &::before {
                        width: 30%;
                    }

                    &:last-of-type {
                        padding-bottom: 10px;
                    }
                }
            }
        }
    }
}

/* Toggle responsive examples on component pages */
.responsive-toggle-container {
    margin: 0;
    padding: 24px;
    border-bottom: 1px solid $grey-light;
    position: relative;
    display: block;

    @media (max-width: 650px) {
        padding: 16px 24px;
    }

    p.toggle-name {
        font-size: 14px;
        text-align: right !important;
        padding-bottom: 0;
        position: relative;
        padding-right: 190px;
        padding-top: 4px;
        padding: 4px 190px 0 0;

        @media (max-width: 650px) {
            display: none !important;
        }
    }

    .mobile-only {
        p {
            font-size: 14px;
            padding: 0 24px 0 0;
            position: relative;
            display: inline;
        }

        @media (min-width: 651px) {
            display: none !important;
        }
    }

    &.hide-mobile {
        @media (max-width: 651px) {
            display: none;
        }
    }

    .switch {
        position: absolute;
        display: inline-block;
        width: auto;
        min-width: 180px;
        height: 32px;
        margin: 0;
        top: 0;
        right: 0;

        input {
            /* Hide default HTML checkbox */
            opacity: 0;
            width: 0;
            height: 0;

            &:checked + .slider {
                &:before {
                    content: "Mobile";
                    -webkit-transform: translateX(80px);
                    -ms-transform: translateX(80px);
                    transform: translateX(80px);
                }
            }
        }

        /* The slider background */
        .slider {
            position: absolute;
            cursor: pointer;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background-color: none;
            border: 1px solid $grey-dark;
            -webkit-transition: 0.4s;
            transition: 0.4s;
            border-radius: 34px;

            &:before {
                position: absolute;
                height: 24px;
                width: 90px;
                padding: 0 16px;
                border-radius: 20px;
                content: "Desktop";
                color: $white;
                left: 4px;
                bottom: 3px;
                background-color: $blue-primary;
                @include font-format($font-family, $font-semibold, normal);
                font-size: 14px;
                text-align: center;
                -webkit-transition: 0.4s;
                transition: 0.4s;
                z-index: 2000;
            }
        }

        &.illustration {
            .slider {
                &:before {
                    content: "Large";
                }
            }

            input:checked + .slider {
                &:before {
                    content: "Small";
                }
            }
        }

        /* Text within slider background */
        .switch-text-mobile {
            color: $grey-darker;
            position: absolute;
            right: 4px;
            bottom: 3px;
            width: 90px;
            text-align: center;

            &:before {
                content: "Mobile";
            }
        }

        .switch-text-desktop {
            color: $grey-darker;
            position: absolute;
            left: 4px;
            bottom: 3px;
            width: 90px;
            text-align: center;

            &:before {
                content: "Desktop";
            }
        }

        .switch-text-small {
            color: $grey-darker;
            position: absolute;
            right: 4px;
            bottom: 3px;
            width: 90px;
            text-align: center;

            &:before {
                content: "Small";
            }
        }

        .switch-text-large {
            color: $grey-darker;
            position: absolute;
            left: 4px;
            bottom: 3px;
            width: 90px;
            text-align: center;

            &:before {
                content: "Large";
            }
        }
    }
}

.mobile-example .example {
    max-width: 320px !important;
    padding: 0;
    margin: 0;
}

/* Toggle element state on form page */
.design-system-card .state-selector {
    text-align: right;
    padding: 16px 24px;
    border-bottom: 1px solid $grey-light;

    .state-selector-intro {
        display: inline-block;
        font-size: 14px;
        padding: 0 8px 0 0;
    }

    label {
        font-size: 14px;
        padding-right: 12px;
    }

    input[type="radio"] {
        opacity: 0;
        width: 0;
        height: 24px;
    }

    span {
        position: relative;
        padding-left: 22px;
        cursor: pointer;

        &:before {
            height: 16px;
            width: 16px;
            content: " ";
            display: inline-block;
            background: url(../assets/radio-not-selected.svg) no-repeat;
            background-size: 16px 16px;
            position: absolute;
            top: 2px;
            left: 0;
        }
    }

    input[checked="checked"] + span:before {
        background: url(../assets/radio-selected.svg) no-repeat;
        background-size: 16px 16px;
    }

    @media (max-width: 650px) {
        display: none;
    }

    @media (max-width: 850px) {

        .state-selector-intro,
        label {
            font-size: 12px;
            padding: 0;
        }
    }
}

