/* Tab content ================================================================ */
.tab-content-container {
    -moz-box-shadow: inset 0 0 5px #333333;
    -webkit-box-shadow: inset 0 0 5px #333333;
    box-shadow: inset 0 0 5px #333333;
    padding: 1rem;
    height: 500px;
    overflow: scroll;

    .tab-section {
        display: flex;
        flex-direction: column;

        .css-code {
            h4 {
                padding-top: 0;
            }
        }
    }
}

.copy-code-button {
    background: #ffffff 0% 0% no-repeat padding-box;
    border: 2px solid #4569a0;
    color: #4569a0;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 2px 4px 0px;
    border-radius: 6px;
    padding: 0.5rem 1rem;
    text-align: center;
    letter-spacing: 0;
    width: 180px;
    align-self: flex-end;
    margin-bottom: 1rem;
    font-weight: 600;
    display: flex;
    justify-content: center;
    align-content: center;

    &.copied {
        color: #297e28;

        span {
            svg {
                path {
                    fill: #297e28;
                }
            }
        }

        &:hover {
            span {
                svg {
                    path {
                        fill: $white;
                    }
                }
            }
        }
    }

    &:focus {
        background: #254f90 0% 0% no-repeat padding-box;
        border: 3px solid #18335c;
        box-shadow: rgba(0, 0, 0, 0.3) 0px 4px 8px 0px;
    }

    &:hover {
        background: #254f90 0% 0% no-repeat padding-box;
        border: 2px solid #18335c;
        transform: translateY(-0.1875rem);
        box-shadow: rgba(0, 0, 0, 0.3) 0px 4px 8px 0px;
        color: $white;

        span {
            svg {
                path {
                    fill: $white;
                }
            }
        }
    }

    svg {
        margin-left: 0.8rem;

        path {
            fill: #4569a0;
        }
    }
}

@media (max-width: 900px) {
    .tabs-component {
        .nav-tabs {
            justify-content: center;
        }
    }

    .copy-code-button {
        align-self: center;
    }
}
